<template>
    <div>
        <div>{{ msg }}</div>
        <div class="container">
            <div
                class="item"
                :class="'num' + item"
                v-for="(item, index) in game.map"
                :key="index"
            >
                {{ item }}
            </div>
        </div>
        <div class="show">
            <div
                class="item"
                :class="'num' + index"
                v-for="index in game.ds"
                :key="index"
            >
                {{ index }}
            </div>
        </div>
        <div class="counter">{{ cnt }}</div>
    </div>
</template>

<script>
export default {
    name: "Game",

    data() {
        return {
            game: new Game2048(),
            cnt: 0,
        };
    },

    mounted() {
        this.enterKeyup();
    },

    destroyed() {
        this.enterKeyupDestroyed();
    },

    methods: {
        enterKey(event) {
            console.log(event.key);
            this.game.user_slip(event.key);
            this.cnt++;
        },
        enterKeyupDestroyed() {
            document.removeEventListener("keyup", this.enterKey);
        },
        enterKeyup() {
            document.addEventListener("keyup", this.enterKey);
        },
    },

    computed: {
        msg() {
            if (this.game.stop == 0) return "游戏开始";
            else return "游戏结束,按R重新游玩";
        },
    },
};

class Game2048 {
    constructor() {
        this.map = [];
        this.start();
        this.test = 0;
        this.ds = [0, 2, 4, 8, 16, 32, 64, 128, 256, 512, 1024, 2048];
    }

    start() {
        this.stop = 0;
        for (let i = 0; i < 16; i++) this.map[i] = 0;
        this.create_point(this.map);
    }

    // 滑动
    slip(dir, map) {
        let flag = 0;

        // 准备工作
        let array = [0, 0, 0, 0];
        let ks = [0, 1, 2, 3];
        if (dir == "s" || dir == "d") ks = [3, 2, 1, 0]; // 倒数

        for (let i = 0; i < 4; i++) {
            if (dir == "w" || dir == "s")
                for (let j = 0; j < 4; j++) array[j] = map[ks[j] * 4 + i];
            else for (let j = 0; j < 4; j++) array[j] = map[ks[j] + i * 4];

            let result = this.combine(array);
            let n_array = result[0];
            if (flag == 0) {
                if (result[1]) flag = result[1];
            } else if (flag == 2) {
                if (result[1] == 1) flag = 1;
            }

            if (dir == "w" || dir == "s")
                for (let j = 0; j < 4; j++) map[ks[j] * 4 + i] = n_array[j];
            else for (let j = 0; j < 4; j++) map[ks[j] + i * 4] = n_array[j];
        }

        return flag;
    }

    // 从右向左合并，只合并一次，左优先
    combine(array) {
        let n_array = [];
        // 紧密排列
        for (let i = 0, j = 0; i < 4; i++) {
            n_array[i] = 0;
            if (array[i] > 0) n_array[j++] = array[i];
        }
        // 移动标记
        let move_flag = 0;
        for (let i = 0; i < 4; i++) {
            if (array[i] != n_array[i]) move_flag = 1;
        }
        // 合并标记
        let combine_flag = 0;
        // 左优先查找合并
        for (let i = 1; i < 4; i++) {
            if (n_array[i] == 0) break;
            if (n_array[i] == n_array[i - 1]) {
                // 合并
                n_array[i - 1] *= 2;
                // 左移
                for (let j = i; j < 3; j++) n_array[j] = n_array[j + 1];
                n_array[3] = 0;
                // 设置合并标记
                combine_flag = 1;
                break;
            }
        }
        if (combine_flag) move_flag = 1;
        let flag = 0;
        if (combine_flag) flag = 1;
        else if (move_flag) flag = 2;
        return [n_array, flag];
    }

    // 用户滑动
    user_slip(dir) {
        if (dir == "t" || dir == "T") {
            this.test = (this.test + 1) % this.ds.length;
            for (let i = 0; i < 16; i++) this.map[i] = this.ds[this.test];
            return;
        }
        // dir分析
        if (dir == "R" || dir == "r") this.start(this.map);
        if (this.stop) {
            return;
        } else {
            if (dir == "W" || dir == "w" || dir == "ArrowUp") dir = "w";
            else if (dir == "A" || dir == "a" || dir == "ArrowLeft") dir = "a";
            else if (dir == "S" || dir == "s" || dir == "ArrowDown") dir = "s";
            else if (dir == "D" || dir == "d" || dir == "ArrowRight") dir = "d";
            else return;
        }

        // 执行方向
        let flag = this.slip(dir, this.map);

        // 结果分析
        if (flag == 0) {
            console.log("no move, no combine");
            this.check();
            return;
        } else if (flag == 1) {
            console.log("combine");
            while (flag == 1) {
                // 略微暂停
                flag = this.slip(dir, this.map);
            }
        } else {
            console.log("move, no combine");
        }

        this.create_point();
        this.check();
    }

    check() {
        console.log("check");
        let empty = 0;
        let test_map = [];
        for (let i = 0; i < 16; i++) {
            test_map[i] = this.map[i];
            if (this.map[i] == 0) {
                empty++;
            }
        }

        if (empty == 0) {
            let flag = 0;

            // 判断是否卡死
            let ds = ["w", "s", "a", "d"];
            for (let i = 0; i < 4; i++) {
                if (this.slip(ds[i], test_map)) {
                    flag = 1;
                    break;
                }
            }
            if (flag == 0) {
                this.stop = 1;
                this.msg = "游戏结束";
            }
        }
    }

    // 生成随机新点
    create_point() {
        let empty = 0;
        let map_max = 0;
        for (let i = 0; i < 16; i++) {
            if (this.map[i] == 0) empty++;
            else if (this.map[i] > map_max) map_max = this.map[i];
        }

        // 随机大小
        let ds = [2, 4, 8, 16, 32];
        let ds_len = 1;
        while (ds_len < 5) {
            if (map_max / ds[ds_len - 1] > 32) ds_len++;
            else break;
        }
        let d = ds[Math.floor(Math.random() * ds_len)];

        // 随机位置
        let a = Math.floor(Math.random() * empty) + 1;
        for (let i = 0; i < 16; i++) {
            if (this.map[i] == 0) a--;
            if (a == 0) {
                this.map[i] = d;
                break;
            }
        }
    }
}
</script>

<style scoped>
.counter {
    opacity: 0;
}
.show {
    display: grid;
    justify-content: center;
    opacity: 0;
    grid-template-columns: repeat(12,90px);
}
.container {
    justify-content: center;
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-columns: repeat(4, 80px);
    grid-template-rows: repeat(4, 80px);
}
.item {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    width: 70px;
    height: 70px;
}
.num0 {
    color: lightgrey;
}
.num2 {
    background-color: orange;
}
.num4 {
    background-color: red;
    color: white;
}
.num8 {
    background-color: black;
    color: white;
}
.num16 {
    background-color: green;
    color: white;
}
.num32 {
    background-color: blue;
    color: white;
}
.num64 {
    background-color: teal;
    color: white;
}
.num128 {
    background-color: gray;
    color: white;
}
.num256 {
    background-color: blueviolet;
    color: white;
}
.num512 {
    background-color: brown;
    color: white;
}
.num1024 {
    background-color:indigo;
    color: white;
}
</style>